import { ApolloClient } from 'apollo-client'
import { InMemoryCache } from 'apollo-cache-inmemory'
import { HttpLink } from 'apollo-link-http'
import GraphNode from '../config/graph.json'
const QueryUrl = AppName => `${GraphNode.AppNode}/subgraphs/name/${GraphNode.APP[AppName]}`

export const client = new ApolloClient({
  link: new HttpLink({
    uri: QueryUrl('SWAP'),
  }),
  cache: new InMemoryCache(),
  shouldBatch: true,
})

export const healthClient = new ApolloClient({
  link: new HttpLink({
    uri: `${GraphNode.HealthNode}/graphql`,
  }),
  cache: new InMemoryCache(),
  shouldBatch: true,
})

export const v1Client = new ApolloClient({
  link: new HttpLink({
    uri: 'https://api.thegraph.com/subgraphs/name/ianlapham/uniswap', // any
  }),
  cache: new InMemoryCache(),
  shouldBatch: true,
})

export const stakingClient = new ApolloClient({
  link: new HttpLink({
    uri: QueryUrl('SWAP'),
  }),
  cache: new InMemoryCache(),
  shouldBatch: true,
})

export const blockClient = new ApolloClient({
  link: new HttpLink({
    uri: QueryUrl('BLOCK')
  }),
  cache: new InMemoryCache(),
})
